/**
 * @fileOverview ‰∏Ä‰∫õÂÖ¨Áî®ÁöÑËøêÁÆóÊñπÊ≥ï
 * @author xile611
 * @date 2015-09-17
 */
import Decimal from 'decimal.js-light';
import { curry } from './utils';
/**
 * Ëé∑ÂèñÊï∞ÂÄºÁöÑ‰ΩçÊï∞
 * ÂÖ∂‰∏≠ÁªùÂØπÂÄºÂ±û‰∫éÂå∫Èó¥[0.1, 1)Ôºå ÂæóÂà∞ÁöÑÂÄº‰∏∫0
 * ÁªùÂØπÂÄºÂ±û‰∫éÂå∫Èó¥[0.01, 0.1)ÔºåÂæóÂà∞ÁöÑ‰ΩçÊï∞‰∏∫ -1
 * ÁªùÂØπÂÄºÂ±û‰∫éÂå∫Èó¥[0.001, 0.01)ÔºåÂæóÂà∞ÁöÑ‰ΩçÊï∞‰∏∫ -2
 *
 * @param  {Number} value Êï∞ÂÄº
 * @return {Integer} ‰ΩçÊï∞
 */

function getDigitCount(value) {
  var result;

  if (value === 0) {
    result = 1;
  } else {
    result = Math.floor(new Decimal(value).abs().log(10).toNumber()) + 1;
  }

  return result;
}
/**
 * ÊåâÁÖßÂõ∫ÂÆöÁöÑÊ≠•ÈïøËé∑Âèñ[start, end)Ëøô‰∏™Âå∫Èó¥ÁöÑÊï∞ÊçÆ
 * Âπ∂‰∏îÈúÄË¶ÅÂ§ÑÁêÜjsËÆ°ÁÆóÁ≤æÂ∫¶ÁöÑÈóÆÈ¢ò
 *
 * @param  {Decimal} start Ëµ∑ÁÇπ
 * @param  {Decimal} end   ÁªàÁÇπÔºå‰∏çÂåÖÂê´ËØ•ÂÄº
 * @param  {Decimal} step  Ê≠•Èïø
 * @return {Array}         Ëã•Âπ≤Êï∞ÂÄº
 */


function rangeStep(start, end, step) {
  var num = new Decimal(start);
  var i = 0;
  var result = []; // magic number to prevent infinite loop

  while (num.lt(end) && i < 100000) {
    result.push(num.toNumber());
    num = num.add(step);
    i++;
  }

  return result;
}
/**
 * ÂØπÊï∞ÂÄºËøõË°åÁ∫øÊÄßÊèíÂÄº
 *
 * @param  {Number} a  ÂÆö‰πâÂüüÁöÑÊûÅÁÇπ
 * @param  {Number} b  ÂÆö‰πâÂüüÁöÑÊûÅÁÇπ
 * @param  {Number} t  [0, 1]ÂÜÖÁöÑÊüê‰∏™ÂÄº
 * @return {Number}    ÂÆö‰πâÂüüÂÜÖÁöÑÊüê‰∏™ÂÄº
 */


var interpolateNumber = curry(function (a, b, t) {
  var newA = +a;
  var newB = +b;
  return newA + t * (newB - newA);
});
/**
 * Á∫øÊÄßÊèíÂÄºÁöÑÈÄÜËøêÁÆó
 *
 * @param  {Number} a ÂÆö‰πâÂüüÁöÑÊûÅÁÇπ
 * @param  {Number} b ÂÆö‰πâÂüüÁöÑÊûÅÁÇπ
 * @param  {Number} x ÂèØ‰ª•ËÆ§‰∏∫ÊòØÊèíÂÄºÂêéÁöÑ‰∏Ä‰∏™ËæìÂá∫ÂÄº
 * @return {Number}   ÂΩìxÂú® a ~ bËøô‰∏™ËåÉÂõ¥ÂÜÖÊó∂ÔºåËøîÂõûÂÄºÂ±û‰∫é[0, 1]
 */

var uninterpolateNumber = curry(function (a, b, x) {
  var diff = b - +a;
  diff = diff || Infinity;
  return (x - a) / diff;
});
/**
 * Á∫øÊÄßÊèíÂÄºÁöÑÈÄÜËøêÁÆóÔºåÂπ∂‰∏îÊúâÊà™Êñ≠ÁöÑÊìç‰Ωú
 *
 * @param  {Number} a ÂÆö‰πâÂüüÁöÑÊûÅÁÇπ
 * @param  {Number} b ÂÆö‰πâÂüüÁöÑÊûÅÁÇπ
 * @param  {Number} x ÂèØ‰ª•ËÆ§‰∏∫ÊòØÊèíÂÄºÂêéÁöÑ‰∏Ä‰∏™ËæìÂá∫ÂÄº
 * @return {Number}   ÂΩìxÂú® a ~ bËøô‰∏™Âå∫Èó¥ÂÜÖÊó∂ÔºåËøîÂõûÂÄºÂ±û‰∫é[0, 1]Ôºå
 * ÂΩìx‰∏çÂú® a ~ bËøô‰∏™Âå∫Èó¥Êó∂Ôºå‰ºöÊà™Êñ≠Âà∞ a ~ b Ëøô‰∏™Âå∫Èó¥
 */

var uninterpolateTruncation = curry(function (a, b, x) {
  var diff = b - +a;
  diff = diff || Infinity;
  return Math.max(0, Math.min(1, (x - a) / diff));
});
export default {
  rangeStep: rangeStep,
  getDigitCount: getDigitCount,
  interpolateNumber: interpolateNumber,
  uninterpolateNumber: uninterpolateNumber,
  uninterpolateTruncation: uninterpolateTruncation
};